import React from 'react'
import styled from '@emotion/styled'
import { css } from '@emotion/core'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'
import { Link, StaticQuery, graphql } from 'gatsby'
import Img from 'gatsby-image'

import config from '../../utils/siteConfig'
import Navbar from './Navbar.js';
import Navigation from './Navigation.js'

import SocialIcons from './SocialIcons.js';

// Styles
import '../../styles/app.css'

/**
* Main layout component
*
* The Layout component wraps around each page and template.
* It also provides the header, footer as well as the main
* styles, and meta data for each page.
*
*/
const DefaultLayout = ({ data, children, bodyClass, isHome }) => {
    const site = data.allGhostSettings.edges[0].node
    const twitterUrl = site.twitter ? `https://twitter.com/${site.twitter.replace(/^@/, ``)}` : null
    const facebookUrl = site.facebook ? `https://www.facebook.com/${site.facebook.replace(/^\//, ``)}` : null

    return (
    <>
        <Helmet>
            <html lang={site.lang} />
            <style type="text/css">{`${site.codeinjection_styles}`}</style>
            <body css={bodyClass} />
        </Helmet>

        <div className="viewport">

            <Navbar site={site} />

            <main css={css`
                font-size: 16px;
                padding: 4rem;
                @media (max-width: 800px) {
                    padding: 1rem;
                }
            `}>
                {/* All the main content gets inserted here, index.js, post.js */}
                {children}
            </main>

            <div css={css`
                background-color: var(--color-bg);
                width: 100%;
            `}>
                {/* The footer at the very bottom of the screen */}
                <footer css={css`
                    max-width: 63rem;
                    box-sizing: border-box;
                    margin: auto;
                    display: flex;
                    justify-content: space-between;
                    flex-wrap: wrap;
                    padding: 4rem;
                    font-size: var(--font-size-small);

                    @media (max-width: 800px) {
                        flex-direction: column-reverse;
                        padding: 1rem;
                        & > div {
                            margin: 20px 0;
                        }
                    }
                `}>
                    <div>
                        <Link to="/">
                            <img src={'/images/orchid-logo-world.svg'} alt={site.title} css={css`
                                display: block;
                                @media (max-width: 800px) {
                                    max-width: 100px;
                                }
                            `} />
                        </Link>
                         {new Date().getFullYear()} Orchid Labs Inc.

                        <p css={css`
                            & > a {
                                font-size: 80%;
                                color: var(--link-color);
                            }
                        `}>
                            <a href="/privacy-policy">Privacy Policy</a> | <a href="/service-terms">Terms of Service</a>
                        </p>
                    </div>
                    <div css={css`
                        li a {
                            font-family: var(--font-sans-serif);
                            font-size: var(--font-size-small);
                            font-weight: var(--font-normal);
                            text-transform: none;
                        }
                    `}>
                        <Navigation data={site.navigation} />
                    </div>
                    <div css={css`
                        li {
                            display: inline-block;
                            margin: .25rem;
                        }
                    `}>
                        <b css={css`
                            font-family: var(--font-family-heading);
                        `}>Follow Us</b>
                        <SocialIcons />
                    </div>
                </footer>

            </div>
        </div>

    </>
    )
}

DefaultLayout.propTypes = {
    children: PropTypes.node.isRequired,
    bodyClass: PropTypes.string,
    isHome: PropTypes.bool,
    data: PropTypes.shape({
        file: PropTypes.object,
        allGhostSettings: PropTypes.object.isRequired,
    }).isRequired,
}

const DefaultLayoutSettingsQuery = props => (
    <StaticQuery
        query={graphql`
            query GhostSettings {
                allGhostSettings {
                    edges {
                        node {
                            ...GhostSettingsFields
                        }
                    }
                }
                file(relativePath: {eq: "ghost-icon.png"}) {
                    childImageSharp {
                        fixed(width: 30, height: 30) {
                            ...GatsbyImageSharpFixed
                        }
                    }
                }
            }
        `}
        render={data => <DefaultLayout data={data} {...props} />}
    />
)

export default DefaultLayoutSettingsQuery
